function str2ab(str) {
    var buf = new ArrayBuffer(str.length * 2); // 2 bytes for each char
    var bufView = new Uint16Array(buf);
    for (var i = 0, strLen = str.length; i < strLen; i++) {
      bufView[i] = str.charCodeAt(i);
    }
    return buf;
  }

class IndexPage extends React.Component{
 
    constructor(){
        super()
        this.state= {
            invoices:null,
            crrInvoice:null,
            apiKey:null,
            adminId: null
        }
        this.setActive= this.setActive.bind(this)
        this.clearActive = this.clearActive.bind(this)
    }
    async setActive(r, e){
        const {apiKey, adminId} = this.state;
        const res = await fetch(
            `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
            {
                mode:"cors",
                method:"POST",
                body: JSON.stringify({
                    method:"GET",
                    path:`${adminId}/sales_invoices/${r.id}/download_pdf.json`,
                    key: apiKey
                })
            }
        )
        const pdf = await res.arrayBuffer();
        this.setState({crrInvoice:pdf })
        
    }

    clearActive(){
        this.setState({crrInvoice:null})
    }
   componentDidMount(){
        this.microsoftTeams = window? window.microsoftTeams: null;
        if(this.microsoftTeams){
            this.microsoftTeams.initialize();
            this.microsoftTeams.getContext(async ({entityId}) =>{
                console.log({entityId})
                const {apiKey, adminId, contact:{value :contactId}} = JSON.parse(entityId);
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                    {
                        mode:"cors",
                        method:"POST",
                        body: JSON.stringify({
                            method:"GET",
                            path:`${adminId}/sales_invoices.json?filter=contact_id:${contactId}`,
                            key: apiKey
                        })
                    }
                )
                const json = await res.json();
                this.setState({
                    invoices: json,
                    adminId,
                    apiKey
                })
            })
        }
    }
    render(){
        const {crrInvoice} = this.state;
        return (
            <div >
                <header style={styles.mainSidePadding}>
                    {crrInvoice?<i onClick={this.clearActive} style={{...styles.arrow, ...styles.left}}></i>:null}
                </header>
                {
                    crrInvoice ? 
                    <div style={styles.mainPadding}>
                        <div style={{...styles.mainTable, ...styles.mainPdf}}>
                            <Document file={{data:crrInvoice}} >
                                <Page pageNumber={1} />
                            </Document>
                        </div>
                    </div>
                    :
                    <div style={styles.mainPadding}>
                        <div style={styles.mainTable}>
                            <DataTable
                                columns={columns}
                                data={testData}
                                onRowClicked={this.setActive}
                                pointerOnHover={true}
                                highlightOnHover={true}
                                noHeader={true}
                            />
                        </div>
                    </div>
                }

            </div>
            
        )
    }
  
}

export default IndexPage;
